      link: 'https://www.meetup.com/edulabs-Munchen/events/269086368/',
      description: '<p>Die edulabs München bieten zum EBmoocplus2020 eine Begleitgruppe an. Wir treffen dazu in den Räumen von Wikipedia in München oder im Café Netzwerk des KJR München.</p> <p>Inhalt des 8. Moduls:<br/>In diesem Modul lernen ErwachsenenbildnerInnen einfache Webinar- und Videokonferenz-Technologien für den Einsatz in der Erwachsenenbildung kennen. Sie erfahren dabei Grundlegendes über Formate wie Online-Podiumsdiskussionen, Online- Gesprächsrunden, Sprech- oder Beratungsstunden sowie „Online- Führungen“, die mit Hilfe von Videokonferenz-Technologien umgesetzt werden können, und sammeln Erfahrungen in der praktischen Nutzung.</p> <p>Was werden wir tun?<br/>t.b.a. (nach dem ersten Treffen)</p> <p>EBmoocplus2020 –das Wichtigste für die digitale Praxis in der Erwachsenenbildung<br/>Ein kostenloser Onlinekurs für Erwachsenenbildner/innen mit Austauschmöglichkeitin Foren, Webinaren und Begleitgruppen</p> <p>Kursinhalt und Ziele<br/>Teilnehmer/innen erwerben Erfahrung und Knowhow über<br/>• Erwachsenenbildung im digitalen Zeitalter<br/>• Formate und Didaktik im digitalen Raum<br/>• Digitale Werkzeuge für die Erwachsenenbildungs-Praxis<br/>• Sicherheit und Datenschutz in der Erwachsenenbildung•Soziale Lernumgebungen im digitalen Raum<br/>• Kritische Medienkompetenz und Erwachsenenbildung•Offene Bildungsressourcen<br/>• Webinare und Live-Videoformate für die Erwachsenenbildung<br/>• Möglichkeiten zum Weiterlernen</p> <p>Ablauf:<br/>• 8 Module in 8 Wochen ab 4. März 2020 (Workload je 3-4 Stunden) + Abschlussmodul (2 Stunden)<br/>• Es wird wöchentlich jeweils ein Modul freigeschaltet.<br/>• Jede Einheit besteht aus einem einführenden Video, ergänzenden Arbeitsmaterialien, einerAufgabenstellung dazu und einem kleinen Test zur (Selbst-)Überprüfung.<br/>• Zusätzlich gibt es ein Forum zur Diskussion über die Kursthemen.<br/>• Außerdem werden begleitend unterschiedliche Typen von Webinaren angeboten<br/>• Alle Materialien werden als Creative Commons Namensnennung 4.0 International Lizenz lizenziert und können auch nach Kursablauf weiter verwendet werden.</p> <p>Mehr Infos zum EBmoocplus2020 unter <a href="https://erwachsenenbildung.at/ebmooc" class="linkified">https://erwachsenenbildung.at/ebmooc</a></p> <p>Anmeldung zum EBmoocplus20202: <a href="https://imoox.at/mooc/local/courseintro/views/startpage.php?id=77" class="linkified">https://imoox.at/mooc/local/courseintro/views/startpage.php?id=77</a></p> <p>EBmooc plus erwachsenenbildung.at 2020<br/>CC BY 4.0 CONEDU #ebmoocplus20<br/>Gefördert aus Mitteln des Bundesministeriums für Bildung, Wissenschaft und Forschung</p> ',
