      description: '<p>BEIDE KINO-EVENTS WERDEN ONLINE MIT LIVE STREAM DES DOKUMENTARFILMS STATTFINDEN !</p> <p>TERMINE:<br/>Sonntag 22. März, 12:00 Uhr (Matinee)<br/>Samstag 04. April, 16:30 Uhr</p> <p>Hier der Zugangslink zum Podcast Portal: <a href="https://pruffme.com/landing/u1359261/tmp1584302153" class="linkified">https://pruffme.com/landing/u1359261/tmp1584302153</a></p> <p>Sahaja Yoga Kultur e.V. präsentiert zum Anlass des 50. Jubiläumsjahrs von Sahaja Yoga, dem 97. Geburtstag der Gründerin Shri Mataji Nirmala Devi:</p> <p>Kino-Event "Nirmala Devi - Freedom &amp; Liberation" mit Dokumentarfilm, Meditation und Filmgespräch mit der Regisseurin Carolin Dassel</p> <p>Euer Team von Free Meditation &amp; Yoga</p> <p>Für ein interaktives online Filmgespräch mit Carolin Dassel bitte nur Google Chrome verwenden, user im Portal anlegen und Emailadresse 1h vor Eventbeginn an [masked] schicken, dann können wir Euch im Portal als "Speaker" anlegen.</p> <p>Film Homepage &amp; Trailer: www.nirmaladevi-film.de<br/>www.meditationmuenchen.org</p> ',
