      link: 'https://www.meetup.com/DE01GOA/events/268591423/',
      description: '<p>Join us for a day hike along the Isar river up towards Grünwald as we cross the river, we again make our way along Isar towards Georgenstein then along Romanstrasse up to the Roman ski jump before return back towards Grünwald.</p> <p>On the once exposed mountain plateau, just above the Georgenstein, there was once a late Roman guard station and later medieval fortifications. From here the river crossing was observed and protection was offered to the population. The huge, up to 10 meters high, multi-level ramparts date from the early Middle Ages. It is believed that the wooden castle built had to do with the Hungarian invasions that threatened the country around 899. The former fields and estates have given way to the forest.</p> <p>360 degree view of Georgenstein <a href="https://preview.tinyurl.com/wakekth" class="linkified">https://preview.tinyurl.com/wakekth</a></p> <p>Hiking Highlights</p> <p>Schöner Platz im Auwald<br/>Alter Holzweg<br/>Schöner Trail an der Isar<br/>Grünwalder Stauwehr<br/>Kraftwerk Baierbrunn<br/>Georgenstein<br/>Schöner Blick auf die Isar und in das Tal<br/>Holzpfade</p> <p>⏱ 4:15hrs Hiking (excluding stoppage time)<br/>↔️ 13.2Km<br/>↗️ 161m (total uphill)<br/>↘️ 162m (total downhill)<br/>🛆 A:619m/D:538m<br/>🥾Hike boots/Trainers<br/>🎚 Intermediate Level<br/>🐌 Pace 3.8Kph</p> <p>📍O-Route: <a href="https://www.outdooractive.com/en/r/127380816" class="linkified">https://www.outdooractive.com/en/r/127380816</a></p> <p>🚏Meeting Point: I will be waiting for everyone outside the main Buchenhain station entrance @ 10.40am</p> <p>✈️🚂️🚐 Transport: Depart: Munich Hfb - Arrive: Buchenhain Bahnhof 10.36am (return from Höllriegelskreuth to Munich)<br/>🚂️ DB Timetable: <a href="https://www.sbb.ch/en/timetable.html" class="linkified">https://www.sbb.ch/en/timetable.html</a></p> <p>🍽 ☕️ 🍻 Cafe/Restaurants: there are cafe/bars in Grünwald.</p> <p>🍎🍌🥪🍫🥤Food/Drinks: You are advised to bring a packed lunch and water (minimum 2L) for the hike.</p> <p>🎒🧥👖👕🧦 🥾👂📸🛌🔦 What You Need: <a href="https://drive.google.com/open?id=1tWEgt30VZUmkH0F9yHOk7qibubN0SOea" class="linkified">https://drive.google.com/open?id=1tWEgt30VZUmkH0F9yHOk7qibubN0SOea</a></p> <p>💶 💷 Payment: €5 Day hike fee. Pay via <a href="https://www.paypal.me/PASHAA/5" class="linkified">https://www.paypal.me/PASHAA/5</a> in Euros</p> <p>🌤Weather: It is generally dry in April but temperatures and conditions can vary alot.<br/>🌤 Local Weather: <a href="https://www.meteoblue.com/en/weather/14-days/grünwald_germany_[masked]" class="linkified">https://www.meteoblue.com/en/weather/14-days/grünwald_germany_[masked]</a></p> <p>📞 Should you need to contact me my number is [masked] (my mobile blocks any numbers which are masked, withheld or private) otherwise you can email at [masked]</p> <p>#GOA-DE #GOA-CH</p> ',
