      description: '<p>Save the Date! Florian Halmburger und weitere Spielleiter werden uns das Agile Fluency Game vorstellen.<br/>Für die Agile Game Night<br/>– Die Beschreibung zum Spiel / der Simulation / dem Thema kommt in Kürze...</p> <p>Das hier sind schon mal die vorläufigen Termine für die zweite Jahreshälfte:<br/>Q3: DO,[masked] + DI,[masked] - DO,[masked]<br/>Q4: DI,[masked] + DO,[masked] + DI,[masked]</p> <p>Du hast ein tolles Spiel und möchtest uns das gern zeigen oder auch ein neues ausprobieren? Gastmoderatoren sind herzlich eingeladen. Sprich uns an.</p> <p>Happy Gaming<br/>Ellen und Sieglinde</p> <p>PS: Du hast auch ein tolles Spiel und möchtest uns das gern zeigen oder auch ausprobieren? Gastmoderatoren sind herzlich eingeladen. Sprich uns an.<br/>----------------------------<br/>Die Agile Game Night ist die Experimentierplattform für Business-Spiele</p> <p>Alle Übungen und Spiele können in Trainings, Workshops, aber auch Scrum-Retrospektiven oder anderen agilen Meetings genutzt werden, im Team neue Lösungsansätze zu entwickeln und Erfahrungen erlebbar zu machen. Unsere Game Night ist für jeden gedacht, der mehr über Agile Methoden erfahren und dabei Spaß haben und spielen möchte. Darüber hinaus können auch Trainer und Coaches davon profitieren, wie sie Spiele in ihre Seminare einbinden und diese vorab schon mal ausprobieren können.</p> <p>Spiele bilden metaphorisch reales Leben ab und helfen, Strukturen nicht nur theoretisch, sondern persönlich zu erleben, erfahren und verinnerlichen.</p> <p>Teilen Sie die Veranstaltung und laden Sie andere ein:</p> <p><a href="http://www.meetup.com/de-DE/Agile-Game-Night/" class="linkified">http://www.meetup.com/de-DE/Agile-Game-Night/</a><br/><a href="http://www.kiwi-COO-business-games.de" class="linkified">http://www.kiwi-COO-business-games.de</a><br/><a href="https://www.facebook.com/groups/agilegamenights/" class="linkified">https://www.facebook.com/groups/agilegamenights/</a></p> ',
